import React, {Component} from "react";
import {Container} from "@material-ui/core";
import HeaderComponent from "../component/header";
import FooterComponent from "../component/footer";

export default class MobileLayout extends Component {

    render() {
        let container_style = null;
        let fullw = window.innerWidth;
        if (fullw < 600) {
            container_style = style.containerSmall;
        } else {
            container_style = style.container;
        }

        return(
            <div style={style.root}>
                <HeaderComponent />
                <Container style={container_style} maxWidth="sm">
                    {this.props.content}
                </Container>
                <FooterComponent />
            </div>
        );
    }
}

const style = {
    container : {
        backgroundColor : "whitesmoke",
        minHeight : "200px",
        paddingTop : "30px",
        paddingBottom : "30px",
    },
    containerSmall : {
        backgroundColor : "whitesmoke",
        minHeight : "150px",
        paddingTop : "10px",
        paddingBottom : "10px",
    },
    root : {}
}